import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, LoadingController, AlertController, ModalController, Searchbar, ToastController } from 'ionic-angular';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { SoapService } from '../soap.service';
 
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass, sc_code } from '../../config';
/**
 * Generated class for the AddPetugasDirektoratPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-petugas-direktorat',
  providers: [SoapService],
  templateUrl: 'add-petugas-direktorat.html',
})
export class AddPetugasDirektoratPage {
  public addPetugasDirektoratForm: FormGroup;
  level:any;
  kategori:any='';
  direktorat:any = '';
  kategoriList:Array<any>=[];
  direktoratList: Array<any>=[];
  isLoading:boolean = true;
  isLoading2:boolean = true;
  errorMessage:any;
  userdataDataIPCContact:any;

  nippList:any = [];
  nippSearchResultList:Array<any> = [];
  nippSelected:any;
  index:any;
  showResult:boolean= false;
  nippInputFocused:any;

  editData:any = '';
  kategoriName:any = '';
  direktoratName:any = '';

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private _fb: FormBuilder,
    public soapService:SoapService,
    public viewCtrl: ViewController,
    public storage: Storage,
    public loadingCtrl:LoadingController,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController
  ) {
    this.editData = navParams.get('editData');
    //console.log(this.editData); 
    if ( this.editDataAda(this.editData)) {       
      this.kategoriName = this.editData['KATEGORI'];
      this.direktoratName = this.editData['DIREKTORAT'];
      this.nippList.push({value:this.editData['NIPP']});
    } else {
      this.nippList.push({value : ''});
    }    
    this.isLoading2 = true;
    storage.get('userdataIPCContact').then((val) => {             
      this.userdataDataIPCContact = val; 
      this.getLevel(this.userdataDataIPCContact['ID_LEVEL']);  
      if (this.userdataDataIPCContact['ID_LEVEL'] == '0') {         
        this.getDataDirektorat();
      } else if (this.userdataDataIPCContact['ID_LEVEL'] == '1') {
        this.direktorat = this.userdataDataIPCContact['ID_DIR'];
        this.getDataKategori();
      } else {
        //console.log('error here');
      }
      
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddPetugasDirektoratPage');
  } 

  getLevel(val) {    
    this.level = val;
    //console.log("level : " + this.level);
    this.isLoading2 = false;
  }

  getDataDirektorat() {  
    let loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",       
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    loading.present();
    this.isLoading = true;
    this.soapService
    .post(api_base_url,'eoffice_data_direktorat_ipcc',{fStream:JSON.stringify(
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      // console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {
        this.direktoratList = []; 
        if (responData['data'].length > 0) {
          for (let i=0; i < responData['data'].length; i++) {
            this.direktoratList.push(responData['data'][i]);
          }                        
        }       
        //this.getDataKategori();

        //console.log(this.direktoratList);       
      } else {        
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();  
        
      }            
      this.isLoading = false;
      loading.dismiss();
    })
    .catch(error => {
      //console.log(error);
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
        duration: 3000,
        position: 'bottom'
      });
      toast.present();     
      this.isLoading = false;
      loading.dismiss();
    });
  }

  getDataKategori() {
    let loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",       
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    loading.present();
    this.isLoading = true;
    this.soapService
    .post(api_base_url,'eoffice_data_kategori_ipcc',{fStream:JSON.stringify(    
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass,                
        id_dir: this.direktorat          
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      //console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {
        this.kategoriList = []; 
        if (responData['data'].length > 0) {
          for (let i=0; i < responData['data'].length; i++) {
            this.kategoriList.push(responData['data'][i]);
          }                        
        }            
        //console.log(this.kategoriList);       
      } else {        
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();  
      }  
      loading.dismiss();
      this.isLoading = false;    
    })
    .catch(error => {
      //console.log(error);
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
        duration: 3000,
        position: 'bottom'
      });
      toast.present(); 

      loading.dismiss();
      this.isLoading = false;             
    });
  }


  addNIPP() { 
    //console.log('clicked');  
    this.nippList.push({value:''});
  }

  removeNIPP(i) {  
    this.nippList.splice(this.index, 1);
  }

  setNIPP(nippSearchResult,i) {

    this.index = i;
    this.nippList[this.index]['value'] = nippSearchResult['NIPP'];
    this.showResult=false;
    //console.log(this.nippList[this.index]);
  }

  checkFocus(i) {
    this.nippInputFocused = i;
    //console.log(this.nippInputFocused);
  }

  getNIPP(index, nipp) {
    //console.log(this.nippList[index]['value']);
    this.soapService
    .post(api_base_url,'eoffice_find_nipp_ipcc',{fStream:JSON.stringify(
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass, 
        search      : this.nippList[index]['value']
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      // console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {
        this.nippSearchResultList = []; 
        if (responData['data'].length > 0) {
          for (let i=0; i < responData['data'].length; i++) {
            this.nippSearchResultList.push(responData['data'][i]);
          }                        
        }
        this.showResult = true;         
        //console.log(this.nippSearchResultList);
        
      } else {
        this.nippSearchResultList = []; 
        // let alert = this.alertCtrl.create({
        //   title: '',
        //   subTitle: 'Gagal mendapatkan pesan (1)',
        //   buttons: ['OK']
        // });
        // alert.present();
      }      
    })
    .catch(error => {
      //console.log(error);
      // let alert = this.alertCtrl.create({
      //   title: '',
      //   subTitle: 'Gagal mendapatkan pesan (2)',
      //   buttons: ['OK']
      // });
      // alert.present();    
      
    });
  } 

  register() {
    //console.log('send data');
    //console.log(this.nippList); 
    let nippData = [];
    for (let i = 0; i < this.nippList.length;i++) {
      if (this.nippList[i]['value'] != '') {
        nippData.push(this.nippList[i]['value']);
      }
    }

    if (nippData.length > 0 && this.direktorat != '' && this.kategori != '' ) {
      this.sendData(nippData);
    } else {
     this.errorMessage = '*Semua field harus diisi'
    }

    //console.log(nippData);
  }

  sendData(nippData) {
    let loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",       
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    loading.present();
    this.isLoading = true;
    this.soapService
    .post(api_base_url,'eoffice_regis_user_ipcc',{fStream:JSON.stringify(
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass,
        nipp: nippData,
        id_dir: this.direktorat,
        id_level: '2',
        user_choice: '1',
        id_kategori: this.kategori   

      }
    // .post(api_base_url,'eoffice_bypass_wso',{fStream:JSON.stringify(
    //   {
    //     // usernameEDI : api_user, 
    //     // passwordEDI : api_pass,        
    //     // id_dir: val['ID_DIR']
    //     sc_type: 'inregisuser',
    //     sc_code: sc_code,        
    //     data : {
    //       nipp: nippData,
    //     id_dir: this.direktorat,
    //     id_level: '2',
    //     user_choice: '1',
    //     id_kategori: this.kategori
    //     }   
     // }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      //console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {
        let toast = this.toastCtrl.create({
          message: 'Register berhasil',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
        this.navCtrl.pop();
      } else {
        this.nippSearchResultList = []; 
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();  
      }  
      loading.dismiss();      
    })
    .catch(error => {
      //console.log(error);
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
        duration: 3000,
        position: 'bottom'
      });
      toast.present(); 

      loading.dismiss();             
    });
  }

  update() {
    if (this.nippList[0]['value'] == '' ) {
      this.errorMessage = '*Semua field harus diisi';
    } else {
      let loading = this.loadingCtrl.create({
        spinner: 'dots',
        content: "Mohon Tunggu...",       
        cssClass: 'transparent',
        //dismissOnPageChange:true
      });
      loading.present();
      this.isLoading = true;
      this.soapService
      .post(api_base_url,'eoffice_upd_user_ipcc',{fStream:JSON.stringify(
        {
          usernameEDI : api_user, 
          passwordEDI : api_pass,
          nippnew : this.nippList[0]['value'],
          nippold : this.editData['NIPP'],
          id_level : '2'       
        }
      )}).then(result => {
        var responData = JSON.parse(String(result));
        //console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          let toast = this.toastCtrl.create({
            message: 'Update Berhasil',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
          this.navCtrl.pop();
        } else {
          this.nippSearchResultList = []; 
          let toast = this.toastCtrl.create({
            message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();  
        }  
        loading.dismiss();      
      })
      .catch(error => {
        //console.log(error);
        let toast = this.toastCtrl.create({
          message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present(); 
  
        loading.dismiss();             
      });
    }
    
  }

  isEmptyObject(obj) { 
    for(var prop in obj) {
       if (obj.hasOwnProperty(prop)) {
          return false;
       }
    }  
    return true;
  }

  editDataAda(val) {
    
    if (typeof val != "undefined") {
      //console.log("true");
      return true;      
    } else {
      //console.log("false");
      return false; 
    }
  }

  cancel() {
    this.viewCtrl.dismiss();
  }

}



